import React, { Component } from "react";

class Contact extends Component {
  render() {
    const { name, email, phone } = this.props
    return (
      <div>
        <h1>{name}</h1>
        <ul>
          <li>Email: {email}</li>
          <li>Phone: +91-{phone}</li>
        </ul>
      </div>
    );
  }
}

export default Contact;
